:host {
  display: block;
}

.inline-date-picker {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 0.5rem;
  background-color: var(--card-background-color, #f8fafc);
  border-radius: 0.5rem;
  border: 1px solid var(--input-border-color, #e2e8f0);
}

// Date Presets
.date-presets {
  display: flex;
  flex-wrap: wrap;
  gap: 0.375rem;
}

.preset-btn {
  padding: 0.375rem 0.75rem;
  font-size: 0.75rem;
  font-weight: 500;
  background-color: var(--input-background-color, #ffffff);
  border: 1px solid var(--input-border-color, #d1d5db);
  border-radius: 1rem;
  color: var(--text-color, #4b5563);
  cursor: pointer;
  transition: all 0.15s ease;

  &:hover {
    background-color: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
    border-color: var(--color-primary-color, #3b82f6);
    color: var(--color-primary-color, #3b82f6);
  }
}

// Date Selectors
.date-selectors {
  display: flex;
  align-items: flex-end;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.date-field {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  flex: 1;
  min-width: 140px;
}

.date-label {
  font-size: 0.6875rem;
  font-weight: 600;
  color: var(--text-color-secondary, #64748b);
  text-transform: uppercase;
  letter-spacing: 0.025em;
}

.date-separator {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 0.5rem;
  color: var(--text-color-secondary, #94a3b8);

  i {
    font-size: 0.875rem;
  }
}

// Compact calendar styling
::ng-deep .compact-calendar {
  .p-datepicker {
    font-size: 0.875rem;
  }

  .p-inputtext {
    font-size: 0.8125rem;
    padding: 0.5rem 0.75rem;
  }

  .p-datepicker-trigger {
    width: 2rem;
  }
}

// Selection Summary
.selection-summary {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.75rem;
  padding: 0.5rem 0.75rem;
  background-color: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
  border-radius: 0.375rem;
  border: 1px solid var(--color-primary-color, #3b82f6);
}

.summary-text {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.8125rem;
  font-weight: 500;
  color: var(--color-primary-color, #3b82f6);

  i {
    font-size: 1rem;
  }
}

::ng-deep .confirm-btn {
  background: var(--color-primary-color, #3b82f6);
  border: none;
  font-weight: 600;

  &:hover {
    filter: brightness(0.9);
  }
}

// Responsive
@media (max-width: 480px) {
  .date-selectors {
    flex-direction: column;
    align-items: stretch;
  }

  .date-separator {
    transform: rotate(90deg);
    padding: 0.25rem 0;
  }

  .date-field {
    min-width: 100%;
  }
}
